<template>
  <div>
  	<table>
  		<tr>
  			<th @click="aaa">名11</th>
  			<th>加一份</th>
  		</tr>
  		<tr v-for="(item,index) in zhi">
  			<td>{{item.name}}</td>
  			<td><button @click="jia(index)">加一份</button></td>
  		</tr>
  	</table>
  </div>
</template>

<script>
import {mapState,mapMutations} from "vuex"
export default {
 computed:{
 	...mapState(['zhi'])
 },
 methods:{
	 ...mapMutations(['jia']),
	 aaa(){
		 localStorage.aa='aa'
		 window.open('http://localhost:8081/#/')
	 }
 }
}
</script>


<style>

</style>



