<template>
	<div>
		<table>
			<tr>
				<th>名</th>
				<th>数量</th>
			</tr>
			<tr v-for="item in zhi">
				<td>{{item.name}}</td>
				<td>{{item.num}}</td>
			</tr>
		</table>
	</div>
</template>

<script>
	import {mapState} from "vuex"
	export default {
	 computed:{
	 	...mapState(['zhi'])
	 }
	}
</script>

