import Vue from 'vue'
import Router from 'vue-router'
import shouye from "@/components/shouye"
import gwc from "@/components/gwc"

Vue.use(Router)

export default new Router({
  routes: [
    {"path":"/",component:shouye},
    {"path":"/gwc",component:gwc}
  ]
})



